/**
 *  Store
 *  (getConfig/saveConfig)
 */
import { useStorage } from '@vueuse/core';
/**  () */
const STORAGE_KEY_UI_CONFIG = 'acu_ui_config_v18';
/**  */
export const NAV_BUTTONS = [
    { id: 'save', icon: 'fa-save', label: '', longPress: 'saveAs' },
    { id: 'saveAs', icon: 'fa-file-export', label: '' },
    { id: 'refresh', icon: 'fa-sync-alt', label: '' },
    { id: 'manualUpdate', icon: 'fa-hand-sparkles', label: '' },
    { id: 'purge', icon: 'fa-eraser', label: '' },
    { id: 'pin', icon: 'fa-thumbtack', label: '' },
    { id: 'toggle', icon: 'fa-compress', label: '' },
    { id: 'openNative', icon: 'fa-external-link-alt', label: '' },
    { id: 'settings', icon: 'fa-cog', label: '', longPress: 'openNative' },
];
/**  */
const DEFAULT_VISIBLE_BUTTONS = ['save', 'refresh', 'manualUpdate', 'settings', 'pin', 'toggle'];
/**  */
const DEFAULT_BUTTON_ORDER = ['save', 'saveAs', 'refresh', 'manualUpdate', 'purge', 'pin', 'toggle', 'settings'];
/**  -  DEFAULT_CONFIG */
export const DEFAULT_CONFIG = {
    theme: 'retro',
    fontFamily: 'default',
    cardWidth: 280,
    fontSize: 13,
    itemsPerPage: 20,
    highlightNew: true,
    highlightColor: 'orange',
    highlightManualColor: 'orange',
    highlightAiColor: 'blue',
    customTitleColor: false,
    titleColor: 'orange',
    layout: 'vertical',
    limitLongText: true,
    showDashboard: true,
    showPagination: true,
    lockPanel: false,
    purgeConfirmation: true,
    gridColumns: 0,
    autoSave: false,
    autoSaveDelay: 5000,
    visibleButtons: DEFAULT_VISIBLE_BUTTONS,
    buttonOrder: DEFAULT_BUTTON_ORDER,
};
/**  */
export const THEMES = [
    { id: 'retro', name: '', icon: 'fa-scroll' },
    { id: 'dark', name: '', icon: 'fa-moon' },
    { id: 'modern', name: '', icon: 'fa-sun' },
    { id: 'forest', name: '', icon: 'fa-tree' },
    { id: 'ocean', name: '', icon: 'fa-water' },
    { id: 'cyber', name: '', icon: 'fa-bolt' },
    { id: 'purple', name: '', icon: 'fa-magic' },
];
/**  */
export const FONTS = [
    { id: 'default', name: ' (Modern)', val: `'Segoe UI', 'Microsoft YaHei', sans-serif` },
    { id: 'hanchan', name: '', val: `"", sans-serif` },
    { id: 'maple', name: 'Maple Mono ()', val: `"Maple Mono NF CN", monospace` },
    { id: 'huiwen', name: ' (Huiwen)', val: `"Huiwen-mincho", serif` },
    { id: 'cooper', name: 'Cooper', val: `"CooperZhengKai", serif` },
    { id: 'yffyt', name: 'YFFYT ()', val: `"YFFYT", sans-serif` },
    { id: 'fusion', name: 'Fusion Pixel ()', val: `"Fusion Pixel 12px M latin", monospace` },
    { id: 'wenkai', name: ' (WenKai)', val: `"LXGW WenKai", serif` },
    { id: 'notosans', name: ' (Noto Sans)', val: `"Noto Sans CJK", sans-serif` },
    { id: 'zhuque', name: ' (Zhuque)', val: `"Zhuque Fangsong (technical preview)", serif` },
];
/**  */
export const HIGHLIGHT_COLORS = {
    orange: { main: '#d35400', bg: 'rgba(211, 84, 0, 0.1)', name: '' },
    red: { main: '#e74c3c', bg: 'rgba(231, 76, 60, 0.1)', name: '' },
    blue: { main: '#3498db', bg: 'rgba(52, 152, 219, 0.1)', name: '' },
    green: { main: '#27ae60', bg: 'rgba(39, 174, 96, 0.1)', name: '' },
    purple: { main: '#9b59b6', bg: 'rgba(155, 89, 182, 0.1)', name: '' },
    teal: { main: '#1abc9c', bg: 'rgba(26, 188, 156, 0.1)', name: '' },
    pink: { main: '#e91e63', bg: 'rgba(233, 30, 99, 0.1)', name: '' },
    lime: { main: '#82c91e', bg: 'rgba(130, 201, 30, 0.1)', name: '' },
    indigo: { main: '#3f51b5', bg: 'rgba(63, 81, 181, 0.1)', name: '' },
    cyan: { main: '#00bcd4', bg: 'rgba(0, 188, 212, 0.1)', name: '' },
    brown: { main: '#795548', bg: 'rgba(121, 85, 72, 0.1)', name: '' },
    grey: { main: '#607d8b', bg: 'rgba(96, 125, 139, 0.1)', name: '' },
};
export const useConfigStore = defineStore('acu-config', () => {
    // ============================================================
    //  -  useStorage  localStorage
    // ============================================================
    /**  -  */
    const config = useStorage(STORAGE_KEY_UI_CONFIG, DEFAULT_CONFIG, localStorage, {
        mergeDefaults: true, // 
    });
    // ============================================================
    // Getters
    // ============================================================
    /**  */
    const theme = computed(() => config.value.theme);
    /**  */
    const fontFamily = computed(() => config.value.fontFamily);
    /**  */
    const fontFamilyValue = computed(() => {
        const font = FONTS.find(f => f.id === config.value.fontFamily);
        return font?.val || FONTS[0].val;
    });
    /**  () */
    const highlightColorObj = computed(() => {
        return HIGHLIGHT_COLORS[config.value.highlightColor] || HIGHLIGHT_COLORS.orange;
    });
    /**  */
    const highlightManualColorObj = computed(() => {
        // 
        const colorKey = config.value.highlightManualColor || config.value.highlightColor;
        return HIGHLIGHT_COLORS[colorKey] || HIGHLIGHT_COLORS.orange;
    });
    /** AI */
    const highlightAiColorObj = computed(() => {
        const colorKey = config.value.highlightAiColor || 'blue';
        return HIGHLIGHT_COLORS[colorKey] || HIGHLIGHT_COLORS.blue;
    });
    /**  */
    const titleColorObj = computed(() => {
        if (config.value.customTitleColor) {
            return HIGHLIGHT_COLORS[config.value.titleColor] || HIGHLIGHT_COLORS.orange;
        }
        return highlightManualColorObj.value;
    });
    /**  CSS  */
    const gridColumnsCss = computed(() => {
        return config.value.gridColumns && config.value.gridColumns > 0
            ? `repeat(${config.value.gridColumns}, 1fr)`
            : 'repeat(auto-fill, minmax(110px, 1fr))';
    });
    // ============================================================
    // Actions
    // ============================================================
    /**
     * 
     * @param updates 
     */
    function updateConfig(updates) {
        config.value = { ...config.value, ...updates };
    }
    /**
     * 
     */
    function resetConfig() {
        config.value = { ...DEFAULT_CONFIG };
    }
    /**
     * 
     * @param themeId  ID
     */
    function setTheme(themeId) {
        updateConfig({ theme: themeId });
    }
    /**
     * 
     * @param fontId  ID
     */
    function setFontFamily(fontId) {
        updateConfig({ fontFamily: fontId });
    }
    /**
     * 
     * @param layout 
     */
    function setLayout(layout) {
        updateConfig({ layout });
    }
    /**
     *  (API)
     * @param colorKey  key
     */
    function setHighlightColor(colorKey) {
        updateConfig({ highlightColor: colorKey, highlightManualColor: colorKey });
    }
    /**
     * 
     * @param colorKey  key
     */
    function setHighlightManualColor(colorKey) {
        updateConfig({ highlightManualColor: colorKey, highlightColor: colorKey });
    }
    /**
     * AI
     * @param colorKey  key
     */
    function setHighlightAiColor(colorKey) {
        updateConfig({ highlightAiColor: colorKey });
    }
    /**
     * 
     * @param colorKey  key
     */
    function setTitleColor(colorKey) {
        updateConfig({ titleColor: colorKey });
    }
    /**
     * 
     */
    function toggleHighlightNew() {
        updateConfig({ highlightNew: !config.value.highlightNew });
    }
    /**
     * 
     */
    function toggleCustomTitleColor() {
        updateConfig({ customTitleColor: !config.value.customTitleColor });
    }
    /**
     * 
     */
    function toggleShowDashboard() {
        updateConfig({ showDashboard: !config.value.showDashboard });
    }
    /**
     * 
     */
    function toggleShowPagination() {
        updateConfig({ showPagination: !config.value.showPagination });
    }
    /**
     * 
     */
    function toggleLockPanel() {
        updateConfig({ lockPanel: !config.value.lockPanel });
    }
    /**
     * 
     */
    function toggleLimitLongText() {
        updateConfig({ limitLongText: !config.value.limitLongText });
    }
    // ============================================================
    //  Actions
    // ============================================================
    /**
     * 
     * @param buttons  ID 
     */
    function setVisibleButtons(buttons) {
        config.value.visibleButtons = buttons;
    }
    /**
     * 
     * @param order  ID 
     */
    function setButtonOrder(order) {
        config.value.buttonOrder = order;
    }
    /**
     * 
     * @param buttonId  ID
     */
    function isButtonVisible(buttonId) {
        return config.value.visibleButtons.includes(buttonId);
    }
    /**
     * 
     * @param buttonId  ID
     */
    function toggleButtonVisibility(buttonId) {
        const current = [...config.value.visibleButtons];
        const index = current.indexOf(buttonId);
        if (index > -1) {
            current.splice(index, 1);
        }
        else {
            current.push(buttonId);
        }
        config.value.visibleButtons = current;
    }
    /**
     * 
     */
    const sortedVisibleButtons = computed(() => {
        const order = config.value.buttonOrder;
        const visible = config.value.visibleButtons;
        //  buttonOrder 
        return order.filter((id) => visible.includes(id));
    });
    /**
     * 
     * @param buttonId  ID
     */
    function getButtonConfig(buttonId) {
        return NAV_BUTTONS.find(btn => btn.id === buttonId);
    }
    return {
        // 
        config,
        // Getters
        theme,
        fontFamily,
        fontFamilyValue,
        highlightColorObj,
        highlightManualColorObj,
        highlightAiColorObj,
        titleColorObj,
        gridColumnsCss,
        // Actions
        updateConfig,
        resetConfig,
        setTheme,
        setFontFamily,
        setLayout,
        setHighlightColor,
        setHighlightManualColor,
        setHighlightAiColor,
        setTitleColor,
        toggleHighlightNew,
        toggleCustomTitleColor,
        toggleShowDashboard,
        toggleShowPagination,
        toggleLockPanel,
        toggleLimitLongText,
        // 
        setVisibleButtons,
        setButtonOrder,
        isButtonVisible,
        toggleButtonVisibility,
        sortedVisibleButtons,
        getButtonConfig,
    };
});
